<template>
  <div class="confirm">
    <main>
      <p>{{ msg }}</p>
      <email-header></email-header>
      <name-header></name-header>
      <regist-button></regist-button>
    </main>
  </div>    
</template>

<script>
import Vue from 'vue'
import {NameHeader, RegistButton, EmailHeader} from './forms/Headers.js'

export default {
  name: 'confirm',
  data () {
    return {
      msg: '下記内容で登録して良いですか？'
    }
  }
}
Vue.component('email-header', EmailHeader)
Vue.component('name-header', NameHeader)
Vue.component('regist-button', RegistButton)
</script>

<style>
main {
  text-align: center;
  margin-top: 40px;
}
</style>



