<template>
  <div class='input'>
    <main>
      <div>
        メールアドレス:<input-email></input-email><br>
        保護者名:<input-text></input-text>
        <input-button></input-button>
      </div>
    </main>
  </div>
</template>

<script>
import Vue from 'vue'
import {InputText, InputButton, InputEmail} from './forms/Headers.js'

export default {
  name: 'input',
  data () {
    return {
    }
  }
}
Vue.component('input-email', InputEmail)
Vue.component('input-text', InputText)
Vue.component('input-button', InputButton)
</script>

<style>
main {
  text-align: center;
  margin-top: 40px;
}
</style>




