import Vue from 'vue'
import PropertyStore from '../../models/PropertyStore.js'

export var NameHeader = Vue.extend({
  name: 'name_header',
  template: '<div class="name">名前:{{ sharedState.state.property.name}}</div>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputText = Vue.extend({
  name: 'name_input',
  template: '<input v-model="sharedState.state.property.name" placeholder="名前を入力してください">名前</input>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var EmailHeader = Vue.extend({
  name: 'email_header',
  template: '<div class="email">メールアドレス:{{ sharedState.state.property.email }}</div>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputEmail = Vue.extend({
  name: 'email_input',
  template: '<input v-model="sharedState.state.property.email" placeholder="メールアドレスを入力してください">メールアドレス</input>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputButton = Vue.extend({
  name: 'input-button',
  template: '<button class="button" v-on:click="validate">確認</button>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  },
  methods: {
    validate: function (event) {
      console.log(this.$el)
      var valid = false
      try {
        valid = this.$data.sharedState.validate()
        if (valid) {
          this.$router.push('/confirm')
        }
      } catch (e) {
        alert(e.message)
      }
    }
  }
})

export var RegistButton = Vue.extend({
  name: 'regist-button',
  template: '<button class="button" v-on:click="regist">登録</button>',
  methods: {
    regist: function (event) {
      this.$router.push('/done')
    }
  }
})



