import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Input from '@/components/Input'
import Confirm from '@/components/Confirm'
import Done from '@/components/Done'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/',
      name: 'Input',
      component: Input
    },
    {
      path: '/confirm',
      name: 'Confirm',
      component: Confirm
    },
    {
      path: '/done',
      name: 'Done',
      component: Done
    }
  ]
})



