var PropertyStore = {
  debug: true,
  state: {
    property: {
      tyep: '1',
      email: '',
      name: ''
    }
  },
  validate () {
    this.debug && console.log('setMessageAction triggered with', this.state)
    console.log(this.state.property.name.length)
    if (this.state.property.name.length <= 0) {
      throw new Error('名前が入力されていません')
    }
    console.log(this.state.property.email.length)
    if (this.state.property.email.length <= 0) {
      throw new Error('メールアドレスが入力されていません')
    }
    return true
  }
}
export default PropertyStore



